.TodoSearch {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 40px;
    width: calc(100% - 62px);
    font-size: 20px;
    text-align: center;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::placeholder {
    color: #585454;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 800;
  }
  
  .TodoSearch:focus {
    outline-color: #61DAFA;
  }